import './index.scss';
import HeadshotImg from '../../assets/images/headshot.jpg';
import { useEffect, useRef } from 'react';
import { DrawSVGPlugin } from 'gsap-trial/DrawSVGPlugin';
import gsap from 'gsap-trial';

const Headshot = () => {
    const frameRef1 = useRef();
    const frameRef2 = useRef();
    const headshotRef = useRef();

    useEffect(() => {
        gsap.registerPlugin(DrawSVGPlugin);

        gsap.fromTo(
            frameRef1.current,
            {
                opacity: 0,
                drawSVG: "40 60%"
            },
            {
                opacity: 1,
                drawSVG: true,
                duration: 3
            }
        );
        gsap.fromTo(
            frameRef2.current,
            {
                opacity: 0,
                drawSVG: "40 60%"
            },
            {
                opacity: 1,
                drawSVG: true,
                duration: 3
            }
        );

        gsap.fromTo(
            headshotRef.current,
            {
                opacity: 0
            },
            {
                opacity: 1,
                delay: 1.5,
                duration: 4
            }
        );
    }, []);

    return (
        <div className='headshot-container'>
            <img ref={headshotRef} className='solid-headshot' src={HeadshotImg} alt='headshot'/>
            <svg xmlns="http://www.w3.org/2000/svg" width="620" height="700" className='svg-container'>
                    <polyline fill='none' ref={frameRef1} pathlength="1000" points="12,500 12,688 200,688"/>
                    <polyline fill='none' ref={frameRef2} pathlength="1000" points="608,200 608,12 420,12"/>
            </svg>

        </div>
    );
}

export default Headshot;